@import "variables";
@import "media";

.product-content-wrapper {
	width: 95%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	@include media("lg") {
		width: 70%;
	}
}
.product-about {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	@include media("md") {
		flex-direction: row;
	}
}
.product-about-left {
	width: 100%;
	display: flex;
	margin-bottom: 20px;
	@include media("md") {
		width: 50%;
		margin-right: 20px;
		margin-bottom: 0px;
	}
	@include media("lg") {
		width: 40%;
	}
}
.product-about-right {
	width: 100%;
	@include media("md") {
		width: 50%;
	}
	@include media("lg") {
		width: 60%;
	}
}
.product-image-inner {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	& .main-image-container {
		height: calc(100% - 100px);
		min-height: 300px;
		display: flex;
		justify-content: center;
		margin-bottom: 20px;
		& a {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			& img {
				height: 80%;
				width: auto;
			}
		}
	}
}
.main-image-container-full-height {
	height: 100% !important;
	margin: 0 !important;
}
.slick-list,
.slick-track {
	height: 100%;
}
.additional-images-wrapper {
	height: 20%;
	max-height: 80px;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	& .additional-images {
		height: 100%;
		width: 80%;
		& .image-additional {
			height: 100%;
			& img {
				max-height: 100%;
				margin: 0 auto;
			}
		}
	}
	& .additional-slider-arrow {
		display: flex;
		justify-content: center;
		align-items: center;
		background: $mainGreen;
		width: 10%;
		height: 100%;
		outline: none;
		border: none;
		cursor: pointer;
		transition: all 0.1s ease;
		&:hover {
			background: $mainGreenLighter;
		}
		&:active {
			background: #fff;
			& i {
				color: $mainGreen;
			}
		}
		& i {
			color: #fff;
			font-size: 2rem;
			transition: all 0.1s ease;
		}
	}
	.additional-left-arrow {
		box-shadow: $shadow;
	}
	.additional-right-arrow {
		box-shadow: $shadow;
	}
	.slick-disabled {
		background: $mainGreenLighter;
		pointer-events: none;
	}
}
.product-about-right {
	display: flex;
	justify-content: center;
}
.column-right-inner {
	width: 100%;
	padding: 30px;
	display: flex;
	flex-direction: column;
	& .product-title {
		font-weight: bold;
		color: #000;
		margin: 0;
	}
	& .text-stock {
		margin-top: 10px;
		color: $grey;
		font-weight: 400;
	}
	& .input-info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		& .control-label {
			color: #000;
			font-size: 2rem;
			font-weight: bold;
		}
		& .price {
			color: $mainGreen;
		}
	}
}
.product-buttons {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.cart-add {
	width: 60%;
}
.wishlist-add {
	width: 35%;
}
.quantity-input-group {
	width: 100%;
	display: flex;
	justify-content: space-between;
	margin: 15px 0;
	& .quantity-btn {
		width: 20%;
		padding: 15px 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: var(--white);
		color: var(--text-dark);
		border: 2px solid var(--white) !important;
		font-size: 2rem;
		font-weight: bold;
		border-radius: 15px;
		border: none;
		transition: all 0.1s linear;
		&:hover {
			border: 2px solid var(--accent-color) !important;
			background: var(--white) !important;
		}
	}
	& .btn-plus {
		&:active {
			background: var(--accent-color);
		}
	}
	& .btn-minus {
		&:active {
			background: var(--accent-color);
		}
	}
	& #input-quantity {
		width: 50%;
		display: inline-block;
		padding: 22px 10px;
		line-height: 140%;
		background: var(--white);
		border: 2px solid var(--white) !important;
		border-radius: 15px;
		border: none;
		text-align: center;
		font-size: 2rem;
		font-weight: bold;
		transition: all 0.1s linear;
		box-shadow: none;

		&:focus {
			border: 2px solid var(--accent-color) !important;
			box-shadow: none;
		}
	}
}
.nav-tabs {
	border-bottom: none;
	margin: 10px 0;
	& .nav-tab-item {
		& a {
			color: #000;
			border-radius: 15px;
			transition: all 0.2s ease;
			&:hover {
				border: 1px solid $greyLight !important;
				background-color: #fff !important;
			}
		}
	}
	& .active {
		& a {
			&:hover {
				background: $greyLight !important;
				font-weight: bold;
				border: none !important;
			}
			background: $greyLight !important;
			font-weight: bold;
			border: none !important;
		}
	}
}
.product-description {
	margin-top: 20px;
	padding: 10px;
	@include media("sm") {
		padding: 50px;
	}
	& td {
		padding: 15px !important;
	}
}
.related-products-wrapper {
	margin-top: 20px;
	display: flex;
	flex-direction: column;
	& .text-related {
		text-align: center;
		font-weight: bold;
	}
}
.slick-prod-wrap {
	& .product-layout {
		width: 97%;
		margin: 0 auto;
	}
}
.slick-dots {
	& li {
		& button::before {
			font-size: 10px !important;
			color: $mainGreen !important;
		}
	}
}
.tab-pane {
	& img,
	& iframe,
	& p {
		max-width: 100%;
		margin: 0 auto;
	}
}
